.Form {
  position: absolute;
  width: 100%;
  height: 74px;
  bottom: 0px;
  background-color: white;
  -webkit-box-shadow: 0px -5px 10px 1px rgba(0,0,0,0.1);
          box-shadow: 0px -5px 10px 1px rgba(0,0,0,0.1);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  z-index: 100;
}

.Form input {
  height: 46px;
  width: 220px;
  margin: 10px;
  font-size: 18px;
  display: table-cell;
  padding: 0 10px 0 10px;
  vertical-align: middle;
  border: 1px solid lightgray;
  border-radius: 5px;
  color: gray;
}

.Form button {
  width: 76px;
  height: 46px;
  font-size: 20px;
  margin: 10px;
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  background-color:  #FCC54A;
  border: none;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}



