.Card {
  width: 300px;
  height: 50px;
  border: 3px solid black;
  padding: 10px 10px 10px 10px;
  margin: 0 auto;
  margin-bottom: 20px;
  color: black;
  border-radius: 10px;
  text-align: center;
}

.Card p{
  width: 300px;
  color: black;
  font-weight: bold;
}



